/*
 * action 
 */

export const INCREASE = 'INCREASE'
export const DECREASE = 'DECREASE'



/*
 * action 
 * increase 
 * decrease 
 */

export function increase(num) {
    return { type: INCREASE ,num}
}

export function decrease(num) {
    return { type: DECREASE ,num}
}


